@use '../../../../../../apps/sanare-app/src/styles/base/mixins' as *;

.inputs-wrapper {
  padding: 16px;
  padding-top: 32px;
  padding-right: 0;
  display: flex;
  gap: 16px;
  // max-width: 920px;
  // margin: 0 auto;
  // width: 30vw;

  .left-input-column {
    flex: 2;
    order: 1;

    .inputs-flex-group {
      display: flex;
      gap: 16px;
      width: 100%;

      .street {
        flex: 4;
      }

      .street-number {
        flex: 1;
      }

      & > * {
        align-self: flex-start;
      }
    }
  }

  .right-input-column {
    height: calc(100vh - 186px);
    overflow-y: scroll;

    flex: 1;
    order: 2;

    // background-color: #faf9fd;
    border-radius: 16px;
    padding: 16px;
    padding-bottom: 0;
    margin-bottom: 16px;
    margin-top: -16px;
  }

  @include respond(compact) {
    flex-direction: column;
    height: 100%;
    padding: 0;
    gap: 0;

    .left-input-column {
      flex: 1;
      order: 2;

      .address-margin {
        margin-top: 16px;
      }

      .inputs-flex-group {
        display: block;
      }
    }

    .right-input-column {
      flex: 1;
      order: 1;
      height: auto;
      overflow-y: visible;
      padding: 16px 0 0 0;
    }
  }
}

.small-width {
  max-width: 920px;
}

.upload-image-placeholder {
  width: 100%;
  height: 130px;
  overflow-y: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 25px;

  .upload-image-skeleton {
    border-radius: 50%;
    height: 120px;
    width: 120px;
    overflow: hidden;
    position: relative;

    &::after {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      transform: translateX(-100%);
      animation: shimmer 1s infinite;
      content: '';
    }

    @keyframes shimmer {
      100% {
        transform: translateX(100%);
      }
    }
  }
}

@media (min-width: 600px) and (max-width: 1048px) {
  .inputs-wrapper {
    flex-direction: column;
    height: calc(100vh - 132px);
    overflow-y: scroll;

    .left-input-column {
      flex: 1;
      order: 2;
    }

    .right-input-column {
      flex: 1;
      order: 1;
      height: auto;
      overflow-y: visible;
    }
  }
}
